.main-nav__menu {
  padding: 0;
  margin: 0;
  position: relative;
  display: none;
}

.main-nav__toggle {
  background: none;
  border: none;
  cursor: pointer;
  width: 24px;
  height: 18px;
  padding: 0;
  margin-bottom: 16px;
  margin-right: 20px;
  position: relative;
  outline: none;

  &--open {
    position: absolute;
    top: 25px;
    right: 0;
    width: 24px;
    height: 18px;
    background: url("../img/mobile-menu.svg") no-repeat 0 0 /cover;
  }

  &--close {
    position: absolute;
    top: 25px;
    right: 3px;
    width: 18px;
    height: 18px;
    background: url("../img/close.svg") no-repeat 0 0 /cover;
  }

  &--close + .main-nav__menu {
    display: block;
  }
}

.main-nav--nojs .main-nav__menu {
  display: block;
}

@media (min-width: $tablet-width) {
  .main-nav__menu {
    display: flex;
  }

  .main-nav__toggle {
    display: none;
  }
}

@media (min-width: $desktop-width) {
  .main-nav {
    width: 42%;
    text-align: right;
  }
}
