.button {
  font-family: $accent-font;
  display: inline-block;
  width: 100%;
  text-align: center;
  padding: 12px;
  color: #ffffff;
  background-color: $accent-color;
  text-decoration: none;
  text-transform: uppercase;

  &:hover,
  &:focus {
    background-color: $hover-color;
    outline: none;
  }

  &:active {
    color: rgba(#ffffff, 0.302);
  }

  &--program {
    margin-bottom: 20px;
    order: 1;
  }

  &--other {
    background-color: $grey-color;
    color: $text-color;
    letter-spacing: 1px;

    &:hover,
    &:focus {
      background-color: #ebebeb;
    }

    &:active {
      background-color: #ebebeb;
      color: rgba($text-color, 0.302);
    }
  }

  &--other-products {
    font-size: 16px;
    padding: 10px;
  }

  &--program-form {
    border: none;
    margin-bottom: 24px;
    font-size: 16px;
    padding: 10px 12px;
  }
}

@media (min-width: $tablet-width) {
  .button {
    &--program {
      padding: 14px 29px;
      width: auto;
      z-index: 1;
      font-size: 20px;
      position: relative;
    }

    &--program-form {
      width: auto;
      padding: 16px 65px;
      margin-right: 60px;
      margin-left: 0;
      margin-bottom: 72px;
      font-size: 19px;
      letter-spacing: 0.4px;
    }

    &--other {
      letter-spacing: 0;
    }
  }
}

@media (min-width: $desktop-width) {
  .button {
    &--program {
      font-size: 20px;
    }

    &--program-form {
      padding: 16px 209px;
      margin-right: 80px;
      margin-bottom: 80px;
    }
  }
}
