.banner {
  text-align: center;
  display: flex;
  flex-direction: column;
  position: relative;

  @include fields();
}

.banner::before {
  content: "";
  width: 100%;
  height: 305px;
  position: absolute;
  top: 0;
  left: 0;
  background: #64a83b url("../img/index-background-mobile@1x.jpg") no-repeat center;
  z-index: -1;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/index-background-mobile@2x.jpg");
    background-size: 318px auto;
  }
}

.banner__text {
  color: #ffffff;
  text-transform: uppercase;
  font-family: $accent-font;
  margin-bottom: 28px;
}

.banner__image {
  margin: 0 auto;
}

@media (min-width: $tablet-width) {
  .banner {
    display: block;
    background-image: none;
    text-align: left;
    padding: 55px 0 0 42px;
    width: 100%;
    max-width: 620px;
    margin: 0 auto;
  }

  .banner::before {
    display: none;
  }

  .banner__text {
    color: #111111;
    font-size: 20px;
    margin-bottom: 45px;
  }

  .banner__image {
    width: 709px;
    height: auto;
    margin: -101px 0 0 -86px;
    z-index: -1;
    display: block;
  }
}

@media (min-width: $desktop-width) {
  .banner {
    max-width: 1300px;
    position: relative;
    padding: 46px 118px 104px;
    margin-bottom: 144px;
  }

  .banner__image {
    position: absolute;
    top: -32px;
    left: 38%;
    width: 546px;
    z-index: 1;
    margin: 0;
  }

  .banner__text {
    margin-bottom: 50px;
  }
}
