/*Шрифты*/

@font-face {
  font-family: "Georgia";
  src: url("../fonts/Georgia.woff") format("woff");
  font-style: normal;
  font-weight: normal;
  font-display: swap;
}

@font-face {
  font-family: "Georgia";
  src: url("../fonts/Georgia-BoldItalic.woff") format("woff");
  font-style: bold;
  font-weight: bold;
  font-display: swap;
}

@font-face {
  font-family: "Georgia";
  src: url("../fonts/Georgia-Italic.woff") format("woff");
  font-style: italic;
  font-weight: normal;
  font-display: swap;
}

/*Базовые стили*/

body {
  font-size: $font-size;
  font-family: $regular-font;
  color: $text-color;
  margin: 0;
  padding: 0;
  min-width: 320px;
  line-height: 1.2;
}

a {
  text-decoration: none;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@media (min-width: $tablet-width) {
  body {
    font-size: $font-size + 2px;
  }
}
