.page-footer {
  background-color: $grey-color;
  text-align: center;

  @include fields();
  padding-top: 35px;
  padding-bottom: 35px;
  margin-top: -3px;
}

.page-footer__logo {
  margin: 0 auto 32px;
  display: block;

  &:hover,
  &:focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}


@media (min-width: $tablet-width) {
  .page-footer {
    @include fields(0, 0);
    padding-top: 40px;
  }

  .page-footer__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include fields(30px, 30px);
  }

  .page-footer__logo {
    margin: 0 0 4px;
  }
}

@media (min-width: $desktop-width) {
  .page-footer__wrapper {
    max-width: 1300px;
    margin: 5px auto 0;

    @include fields(40px, 40px);
  }
}
