  font-style: bold;
  font-weight: bold;
  font-display: swap; }

@font-face {
  font-family: "Georgia";
  src: url("../fonts/Georgia-Italic.woff") format("woff");
  font-style: italic;
  font-weight: normal;
  font-display: swap; }

/*Базовые стили*/
body {
  font-size: 14px;
  font-family: "Arial", sans-serif;
  color: #444444;
  margin: 0;
  padding: 0;
  min-width: 320px;
  line-height: 1.2; }

a {
  text-decoration: none; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

@media (min-width: 768px) {
  body {
    font-size: 16px; } }

.page-header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #e6e6e6;
  margin-top: 12px; }

.page-header__link {
  display: flex;
  justify-content: space-between;
  width: 60%;
  align-items: baseline;
  margin-left: 20px; }

.page-header__logo {
  margin-bottom: 13px; }
  .page-header__logo--text {
    margin-bottom: 4px; }
  .page-header__logo:hover, .page-header__logo:focus {
    opacity: 0.8; }
  .page-header__logo:active {
    opacity: 0.6; }

@media (min-width: 768px) {
  .page-header {
    width: auto;
    padding-left: 30px;
    padding-right: 30px;
    margin-top: 25px;
    border: none; }
  .page-header__link {
    display: block;
    width: auto;
    margin-left: 0; }
  .page-header__logo {
    margin-bottom: 0; }
    .page-header__logo--text {
      display: none; } }

@media (min-width: 1300px) {
  .page-header {
    max-width: 1300px;
    margin: 50px auto 52px;
    padding-left: 40px;
    padding-right: 40px; } }

.banner {
  text-align: center;
  display: flex;
  flex-direction: column;
  position: relative;
  padding-left: 20px;
  padding-right: 20px; }

.banner::before {
  content: "";
  width: 100%;
  height: 305px;
  position: absolute;
  top: 0;
  left: 0;
  background: #64a83b url("../img/index-background-mobile@1x.jpg") no-repeat center;
  z-index: -1; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .banner::before {
      background-image: url("../img/index-background-mobile@2x.jpg");
      background-size: 318px auto; } }

.banner__text {
  color: #ffffff;
  text-transform: uppercase;
  font-family: "Georgia", "Franklin Gothic Medium", sans-serif;
  margin-bottom: 28px; }

.banner__image {
  margin: 0 auto; }

@media (min-width: 768px) {
  .banner {
    display: block;
    background-image: none;
    text-align: left;
    padding: 55px 0 0 42px;
    width: 100%;
    max-width: 620px;
    margin: 0 auto; }
  .banner::before {
    display: none; }
  .banner__text {
    color: #111111;
    font-size: 20px;
    margin-bottom: 45px; }
  .banner__image {
    width: 709px;
    height: auto;
    margin: -101px 0 0 -86px;
    z-index: -1;
    display: block; } }

@media (min-width: 1300px) {
  .banner {
    max-width: 1300px;
    position: relative;
    padding: 46px 118px 104px;
    margin-bottom: 144px; }
  .banner__image {
    position: absolute;
    top: -32px;
    left: 38%;
    width: 546px;
    z-index: 1;
    margin: 0; }
  .banner__text {
    margin-bottom: 50px; } }

.button {
  font-family: "Georgia", "Franklin Gothic Medium", sans-serif;
  display: inline-block;
  width: 100%;
  text-align: center;
  padding: 12px;
  color: #ffffff;
  background-color: #68b738;
  text-decoration: none;
  text-transform: uppercase; }
  .button:hover, .button:focus {
    background-color: #5eaa2f;
    outline: none; }
  .button:active {
    color: rgba(255, 255, 255, 0.302); }
  .button--program {
    margin-bottom: 20px;
    order: 1; }
  .button--other {
    background-color: #f2f2f2;
    color: #444444;
    letter-spacing: 1px; }
    .button--other:hover, .button--other:focus {
      background-color: #ebebeb; }
    .button--other:active {
      background-color: #ebebeb;
      color: rgba(68, 68, 68, 0.302); }
  .button--other-products {
    font-size: 16px;
    padding: 10px; }
  .button--program-form {
    border: none;
    margin-bottom: 24px;
    font-size: 16px;
    padding: 10px 12px; }

@media (min-width: 768px) {
  .button--program {
    padding: 14px 29px;
    width: auto;
    z-index: 1;
    font-size: 20px;
    position: relative; }
  .button--program-form {
    width: auto;
    padding: 16px 65px;
    margin-right: 60px;
    margin-left: 0;
    margin-bottom: 72px;
    font-size: 19px;
    letter-spacing: 0.4px; }
  .button--other {
    letter-spacing: 0; } }

@media (min-width: 1300px) {
  .button--program {
    font-size: 20px; }
  .button--program-form {
    padding: 16px 209px;
    margin-right: 80px;
    margin-bottom: 80px; } }

.page-footer {
  background-color: #f2f2f2;
  text-align: center;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 35px;
  padding-bottom: 35px;
  margin-top: -3px; }

.page-footer__logo {
  margin: 0 auto 32px;
  display: block; }
  .page-footer__logo:hover, .page-footer__logo:focus {
    opacity: 0.8; }
  .page-footer__logo:active {
    opacity: 0.6; }

@media (min-width: 768px) {
  .page-footer {
    padding-left: 0;
    padding-right: 0;
    padding-top: 40px; }
  .page-footer__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 30px;
    padding-right: 30px; }
  .page-footer__logo {
    margin: 0 0 4px; } }

@media (min-width: 1300px) {
  .page-footer__wrapper {
    max-width: 1300px;
    margin: 5px auto 0;
    padding-left: 40px;
    padding-right: 40px; } }

.menu {
  display: flex;
  width: auto;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0; }

.menu__item {
  margin-left: 33px;
  padding-bottom: 5px; }

.menu__link {
  font-size: 20px;
  color: #000; }
  .menu__link:hover, .menu__link:focus {
    color: rgba(0, 0, 0, 0.6); }
  .menu__link:active {
    color: rgba(0, 0, 0, 0.3); }
  .menu__link--active-front {
    border-color: #000; }

.main-nav__menu {
  padding: 0;
  margin: 0;
  position: relative;
  display: none; }

.main-nav__toggle {
  background: none;
  border: none;
  cursor: pointer;
  width: 24px;
  height: 18px;
  padding: 0;
  margin-bottom: 16px;
  margin-right: 20px;
  position: relative;
  outline: none; }
  .main-nav__toggle--open {
    position: absolute;
    top: 25px;
    right: 0;
    width: 24px;
    height: 18px;
    background: url("../img/mobile-menu.svg") no-repeat 0 0/cover; }
  .main-nav__toggle--close {
    position: absolute;
    top: 25px;
    right: 3px;
    width: 18px;
    height: 18px;
    background: url("../img/close.svg") no-repeat 0 0/cover; }
  .main-nav__toggle--close + .main-nav__menu {
    display: block; }

.main-nav--nojs .main-nav__menu {
  display: block; }

@media (min-width: 768px) {
  .main-nav__menu {
    display: flex; }
  .main-nav__toggle {
    display: none; } }

@media (min-width: 1300px) {
  .main-nav {
    width: 42%;
    text-align: right; } }
