.menu {
  display: flex;
  width: auto;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu__item {
  margin-left: 33px;
  padding-bottom: 5px;
}

.menu__link {
  font-size: 20px;
  color: #000;

  &:hover,
  &:focus {
    color: rgba(#000, 0.6);
  }

  &:active {
    color: rgba(#000, 0.3);
  }

  &--active-front {
    border-color: #000;
  }
}
