@mixin fields($padding-left: 20px, $padding-right: 20px) {
  padding-left: $padding-left;
  padding-right: $padding-right;
}

@mixin upper-title {
  font-family: $accent-font;
  text-transform: uppercase;
  color: #111111;
  margin: 0;
  font-weight: normal;
}

@mixin size($width: 50px, $height: 50px) {
  width: $width;
  height: $height;
}
