.page-header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  border-bottom: $border;
  margin-top: 12px;
}

.page-header__link {
  display: flex;
  justify-content: space-between;
  width: 60%;
  align-items: baseline;
  margin-left: 20px;
}

.page-header__logo {
  margin-bottom: 13px;

  &--text {
    margin-bottom: 4px;
  }

  &:hover,
  &:focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

@media (min-width: $tablet-width) {
  .page-header {
    width: auto;

    @include fields(30px, 30px);
    margin-top: 25px;
    border: none;
  }

  .page-header__link {
    display: block;
    width: auto;
    margin-left: 0;
  }

  .page-header__logo {
    margin-bottom: 0;

    &--text {
      display: none;
    }
  }
}

@media (min-width: $desktop-width) {
  .page-header {
    max-width: 1300px;
    margin: 50px auto 52px;

    @include fields(40px, 40px);
  }
}
